:root {
  --success-color: green;
  --edit-color: #1769aa;
  --delete-color: #b23c17;
  --hover-bg: #c5c5c5;
}

* {
  box-sizing: border-box;
}

.Flex {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  background: #cfd8dc;
}

.custom-card {
  max-height: 80vh;
  overflow-y: auto !important;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  padding: 1rem;
  background-color: #fff;
  transition: transform 0.3s, box-shadow 0.3s;
}

.custom-card:hover {
  box-shadow: 8px 8px 20px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}

.custom-card::-webkit-scrollbar {
  width: 10px;
}

.custom-card::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.custom-card::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 6px;
}

.custom-card::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.Btn {
  transition: all 0.3s !important;
}

.Btn:hover {
  background-color: var(--hover-bg) !important;
  box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.4) !important;
}

.Suc-Btn {
  border-color: var(--success-color) !important;
}

.Edi-Btn {
  border-color: var(--edit-color) !important;
}

.Del-Btn {
  border-color: var(--delete-color) !important;
}

.Dialog-btn:hover {
  color: #e53935;
}

.Tog-Btns {
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}

.Tog-A,
.Tog-D,
.Tog-U {
  color: white !important;
  border: 1px solid rgba(0, 0, 0, 0.349) !important;
  border-radius: 5px !important;
}

.Tog-A {
  background-color: #1e88e5 !important;
}

.Tog-D {
  background-color: #43a047 !important;
  margin: 0 5px !important;
}

.Tog-U {
  background-color: #e53935 !important;
}

.Tog-A.Mui-selected,
.Tog-D.Mui-selected,
.Tog-U.Mui-selected {
  background-color: rgba(173, 173, 173, 0.534) !important;
  color: black !important;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.295);
}

@font-face {
  font-family: Roboto;
  src: url(/public/fonts/Roboto-Black.ttf);
  font-weight: 900;
}

@font-face {
  font-family: Roboto;
  src: url(/public/fonts/Roboto-Light.ttf);
  font-weight: normal;
}
